export default {
    getCharForNumber(i) {
        return "ABCDEFGHIJKLMNOPQRSTUVWXYZ".substring(i, i + 1);
    },

    getNumberForChar(c) {
        let c1 = c.substring(0, 1);
        return "ABCDEFGHIJKLMNOPQRSTUVWXYZ".indexOf(c1);
    },

    renderColor(_status) {
        switch (_status) {
            case 0: {
                //
                return '#8BC34A';
            }
            case 1: {
                //
                return '#f44336';
            }
            case 2: {
                //
                return '#FFEB3B';
            }
            case 3: {
                //disable
                return '#9E9E9E';
            }
            case 4: {
                //
                return '#9E9E9E';
            }
            case 5: {
                //
                return '#9E9E9E';
            }
        }
    },

    renderText(_status) {
        switch (_status) {
            case 0: {
                //
                return 'Off';
            }
            case 1: {
                //
                return 'On';
            }
            case 2: {
                //
                return 'Fault';
            }
            case 3: {
                //disable
                return 'Disabled';
            }
            case 4: {
                //
                return 'Offline';
            }
            case 5: {
                //
                return 'N/A';
            }
        }
    }

}


