import { observable, action } from 'mobx';

export default class ModalsState {
    @observable title = undefined;
    @observable content = undefined; //
    okFunc = undefined;

    @action setModal(_title, _content, _okFunc) { //action
        this.title = _title;
        this.content = _content;
        this.okFunc = _okFunc;
    }

    @action resetModal() { //action
        this.title = undefined;
        this.content = undefined;
        this.okFunc = undefined;
    }


    @observable alertContent = ''; //
    @observable alertType = 0; //
    @action setAlert(_alertType, _alertContent) { //action
        this.alertType = _alertType;
        this.alertContent = _alertContent; 
    }

} 


